"use client";

import AdventureBookingCard from "@/app/components/BookingCard";
import CartCard from "../components/Billing";
import { ArrowLeft } from "lucide-react";
import { useRouter, useSearchParams } from "next/navigation";
import { Button } from "@/components/ui/button";
import { use, useEffect, useState } from "react";

interface slot {
  _id: string;
  adventureId: string;
  date: string;
  time: string;
  availableSeats: number;
  capacity: number;
  bookedSeats: number;
}

interface Adventure {
  _id: string;
  adventure: string;
  places: string;
  amount: number;
  imageUrl: string;
  discription: string;
}
export default function Home() {
  const searchParams = useSearchParams();
  const experienceId = searchParams.get("id");
  const [slots, setSlots] = useState<slot[]>([]);
  const [unavailableSlots, setUnavailableSlots] = useState<string[]>([]);
  const [adventures, setAdventures] = useState<Adventure[]>([]);

  const router = useRouter();
  let slotsData: slot[] = [];
  let adventuresData: Adventure[] = [];
  useEffect(() => {
    if (!experienceId) return;

    fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/details?experienceId=${experienceId}`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    )
      .then((response) => response.json())
      .then((data: { slots: slot[]; adventure: Adventure[] }) => {
        console.log("Fetched details:", data);
        slotsData = data.slots;
        adventuresData = data.adventure;
        const unavailable = data.slots
          .filter((slot) => slot.availableSeats <= 0)
          .map((slot) => {
            const date = slot.date;
            const time = new Date(slot.time).toISOString().slice(11, 16); // HH:mm
            return `${date}-${time}`;
          });

        setUnavailableSlots(unavailable);
        console.log("adventure data:", adventuresData);
        console.log("slots data:", slotsData);
      })
      .catch((error) => {
        console.error("Error fetching details:", error);
      });
  }, [experienceId]);


  return (
    <div className="min-h-screen flex flex-col">
      {/* Back Button */}
      <div className="p-4 md:p-8">
        <Button
          variant="ghost"
          className="flex items-center gap-2 font-bold"
          onClick={() => router.push("/")}
        >
          <ArrowLeft /> Adventure
        </Button>
      </div>

      {/* Main content */}
      <div className="flex flex-col md:flex-row md:gap-6 md:p-8">
        {/* Left Section - Adventure */}
        <div className="w-full md:w-1/2 p-4 md:p-0 flex justify-center">
          <AdventureBookingCard
            image={adventuresData[0]?.imageUrl || ""}
            adventureName={adventuresData[0].adventure || "Adventure"}
            discription={adventuresData[0]?.discription || ""}
            about="This adventure includes guided scuba diving sessions, safety training, and all necessary equipment."
            unavailableSlots={unavailableSlots}
          />
        </div>

        {/* Right Section - Cart */}
        <div className="w-full md:w-1/2 p-4 md:p-0 flex justify-center">
          <div className="w-full max-w-md h-auto md:h-[80vh]">
            <CartCard startingAmount={4999} initialQuantity={1} />
          </div>
        </div>
      </div>
    </div>
  );
}
